import React, { useEffect, useMemo } from 'react'
import SkeletonStyle from '../util/SkeletonStyle'
import SkeletonWrapper from '../util/SkeletonWrapper'

const SkeletonRectangle = ({
  inheritClass,
  children,
  hideWhen = null,
  showWhen = null,
  ...props
}) => {
  const reveal = useMemo(() => {
    if (hideWhen === null && showWhen === null) {
      return children.length > 0
    }
    if (hideWhen === null && showWhen === true) {
      return true
    }
    return false
  }, [children, hideWhen, showWhen])

  useEffect(() => {
    window.alert('HKIH')
  }, [])

  return (
    <SkeletonWrapper speed={props.speed}>
      {reveal ? (
        <>{children}</>
      ) : (
        <div
          className={`${inheritClass} skeleton-ui-rectangle skeleton-ui-loader`.trim()}
          style={props.style}
        />
      )}
      <SkeletonStyle
        componentName='rectangle'
        css={`
          .skeleton-ui-rectangle {
            height: 1rem;
            background-color: #fefefe;
          }
        `}
      />
    </SkeletonWrapper>
  )
}

SkeletonRectangle.defaultProps = {
  inheritClass: 'skeleton-ui-component',
  speed: 3,
  style: {}
}

export default SkeletonRectangle
